"use client";
import Image from "next/image";
import { ProductData } from "@/types";
import { CopyIcon } from "@/assets/icons";
import toast from "react-hot-toast";

export const ProductCard = ({ product }: { product: ProductData }) => {

  const handleCopy = () => {
    navigator.clipboard.writeText(product.title).then(() => {
      toast.success(`${product.title} copied`);
    });
  };

  return (
    <div className="p-[13px] border border-superSilver rounded-[10px]">
      <div className="flex flex-col items-center text-center">
        <Image
          src={product.image}
          alt={product.title}
          width={200}
          height={200}
          className="mb-2"
        />
        <div className="flex justify-between gap-2 items-center">
          <h3 className="text-sm font-bold">{product.title}</h3>
          <span className="cursor-pointer relative" onClick={handleCopy}>
            <CopyIcon />
          </span>
        </div>
        <p className="text-gray-500">{product.article}</p>
        <p className="text-lg font-semibold text-blue-600">
          {product.price} сум
        </p>
      </div>
    </div>
  );
};
